@import nullstyle
@import mediaqueries
//import fonts
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap')
$mainFont: "Orbitron", sans-serif
//colors
$tial: #2EA89C
$cyan:#00BCD4
$brown:#795548
$lime:#b4c237
$green:#4CAF50
$orange:#FF9800
$purple:#9C27B0
$deeppurple:#673AB7
$grey: #e9e9e9
$amber:#FFC107
$bluegrey:#607D8B
$black: #000
$red: #F44336
$pink: #FF4081
$white: #fff
.s2
	background-color: $cyan
.s4
	background-color: $brown
.s8 
	background-color: $lime
.s16
	background-color: $green
.s32 
	background-color: $orange
.s64 
	background-color: $purple
.s128 
	background-color: $deeppurple
.s256 
	background-color: $pink
.s512 
	background-color: $amber
.s1024 
	background-color: $bluegrey
.s2048
	background-color: $red
//	common styles
::-webkit-scrollbar
	width: 0px
	background: transparent
html
	-ms-overflow-style:none
	scrollbar-width:none
	overflow: hidden
	background-color: $tial
	position: fixed
	touch-action: none
	color: $white
	+MW1024 
		font-size: 8px
	+MW768
		font-size: 6px
	+MW414
		font-size: 5px
	+MW320
		font-size: 4px
	+L823
		font-size: 6px
body
	font-family: $mainFont
	background-color: $tial
	width: 100%
	height: 100%
	overflow: hidden
	position: fixed
.wrapper 
	background-color: $tial
	width: 100%
	height: 100%
	display: flex
	justify-content: center
	align-items: center
	flex-direction: column
	overflow: hidden
.game 
	background-color: $tial
	width: 100%
	height: 100%
	max-width: 65rem
	display: flex
	justify-content: center
	align-items: center
	flex-direction: column
	position: relative
	overflow: hidden
	+L823
		flex-direction: row
		max-width: 100%
		justify-content: space-evenly
	+MW414 
		max-width: 75rem
	&:hover 
		cursor: pointer
//table
.table
	width: 60rem
	height: 60rem
	display: grid
	grid-template-columns: repeat(4,1fr)
	border: solid $white .6rem
	border-radius: 3%
	box-shadow: .5rem .5rem 2rem black
	background-color: #fff
	&__cell
		width: 100%
		height: 100%
		border: solid $white .6rem
		position: relative
		border-radius: 7%
		background-color: hsla(0, 0%, 0%, .3)
	+MW414 
		width: 70rem
		height: 70rem
	+L823
		width: 50rem
		height: 50rem
.square
	width: 100%
	height: 100%
	top: 0%
	left: 0%
	color:	white
	position: absolute
	font-size: 4rem
	font-weight: 800
	display: flex
	justify-content: center
	align-items: center
	transition: .1s
	opacity: 100%
	border-radius: 6%
	+L823 
		font-size: 3rem
.new 
	animation-name: new
	animation-duration: .3s
@keyframes new
	0%
		transform: scale(0)
	100%
		transform: scale(1)
.merged 
	animation-name: scaler
	animation-duration: .5s
	transition:.1s
@keyframes scaler
	0%
		transform: scale(1)
	50%
		transform: scale(1.1)
	100% 
		transform: scale(1)
//info
.info 
	display: grid
	grid-template-columns: 1fr 2fr 1fr 
	grid-row-gap: 3rem
	margin-bottom: 3rem
	max-width: 65rem
	+MW414 
		max-width: 70rem
		width: 100%
	+L823
		width: 30%
		grid-template-columns: 1fr 1fr
.title 
	font-size: 4rem
	font-weight: 800
	max-height: 7rem
	align-self: center
	+L568  
		font-size: 2rem
.bg-sound 
	width: 64px
	height: 64px
	background: url('../img/css_sprites.png') -94px -10px
	&:active 
		outline: none
.bg-no_sound 
	width: 64px
	height: 64px
	background: url('../img/css_sprites.png') -10px -10px
	&:active 
		outline: none
.sound 
	display: flex
	justify-content: center
	max-height: 7rem
	transform: scale(.8)
	outline: none
	+MW1366
		visibility: hidden
	+MW768
		transform: scale(.4)
	+L823 
		transform: scale(.4)
	&:hover 
		cursor: pointer
	&:active 
		outline: none
.score
	padding: .5rem 3rem
	text-align: right
	border: white solid .4rem
	border-radius: 8%
	max-height: 7rem
	p 
		font-size: 2rem
		font-weight: 800
		text-transform: uppercase
	span 
		font-size: 2rem
		animation-name: smoothNum
		animation-duration: .5s
@keyframes smoothNum
	0%
		opacity: 0%
	100%
		opacity: 100%
.new-game-btn
	grid-column-start: 1
	grid-column-end: 4
	color: white 
	font-size: 2rem 
	border: white solid .4rem 
	width: 100%
	text-align: center
	padding: 2rem 0rem
	text-transform: uppercase
	border-radius: 8%
	&:hover 
		background-color: #ffffff
		color: $bluegrey
		cursor: pointer
		transition:.2s
	+MW768 
		&:hover
			background-color: $tial
			color: inherit
//message
.message 
	background-color: $grey
	width: 80%
	height: 80%
	position:  absolute
	z-index: 10000
	opacity: .9
	font-size: 4rem
	font-weight: 800
	text-transform: uppercase
	display: flex
	justify-content: center
	align-items: center
	flex-direction: column
	transition:1s
	color:$black
	text-align: center
	border-radius: 2rem
	span 
		font-size: 2.5rem
		padding: 2rem
	button 
		font-size: 2rem 
		border:.2rem solid $black
		padding: 1rem 4rem
		text-transform: uppercase
		font-weight: 800
.settings-icon
	position: absolute
	top: 2%
	right:3%
	font-size: 4rem
	z-index: 1
.settings-content
	position: absolute
	width: 80%
	height: 80%
	max-width: 100rem
	font-size: 4rem
	text-align: center
	line-height: 8rem
	color: $black
	background-color: $grey
	border: .4rem solid $black
	border-radius: 1rem
	z-index: 10000
	display: flex
	flex-direction: column
	justify-content: center
	align-items: center
	padding: 0rem 2rem
	transition:.2s
	&:hover 
		cursor: pointer
	span 
		text-transform: uppercase
		font-weight: 800 
		&:hover
			cursor: pointer
	p 
		font-size: 3.5rem
	.mail 
		a
			border: .4rem solid $black
			padding: 1rem 2rem
			margin-top: 3rem
			font-size: 3rem
			font-weight: 800
.IE 
	width: 100vw
	height: 100vh
	position: absolute
	background-color: $tial
	top:0% 
	left:0%
	z-index: 1000000
	display: flex
	align-items: center
	justify-content: center
	font-size: 4rem
.hide 
	opacity: 0%
	transition:.3s
	z-index: -1




